<template>
	<div class="cartcontrol">
	    <transition name='move'>
		<div class="reduce" v-show = 'food.count' @click.stop = 'decrement' ><i class="icon iconfont icon-jian"></i></div>
		</transition> 
		<div class="count" v-show = 'food.count'>{{food.count}}</div>
		<div class="add"  @click.stop = 'increment'> <i class="icon iconfont icon-jia"></i></div>
	</div>
</template>


<script>
    import Vue from 'vue';
	import {mapGetters} from 'vuex'
	export default {
		props:['food','onoff','totalCount'],
		methods:{
			increment(){
				if( !this.food.count ){
					Vue.set(this.food , 'count' ,1);

				}else{
					this.food.count ++;
				}
			},
			decrement(){
				if( this.food.count ){
					this.food.count --;
				}
			}
		}
	}
</script>

<style lang='less' scoped>
	.cartcontrol{
		width: 1.55rem;
		// border: 1px solid #000;
		height: 0.3rem;
		position: relative;
		  .move-enter-active, .move-leave-active {
            transition: all 0.5s;
        }
        .move-leave-active {
            transition: all 0.5s;
            transform:translate(30px) rotate(90deg);
        }
        // count>0时候的动画，v-show显示时的动画效果
        .move-enter, .move-leave-active {
            transform:translate(20px) rotate(90deg);
            opacity: 0
        }
		.add{
			position: absolute;
			right: 0;
			color:rgb(0,160,220);

		}
		.reduce{
			position: absolute;
			left: 0;
			color:rgb(0,160,220);
		}
		.count{
			position: absolute;
			left: 0.54rem;
			font-size: 0.2rem;
			color: rgb(147,153,159);

		}
		div{
			width: 0.55rem;
			height: 0.55rem;
			line-height: 0.56rem;
			text-align: center;
			display: inline-block;
			
		}
	}
</style>


