<template>
	<div class="sellers" ref = "menuWrapper" >
		<div>
			<div class="content">
				<div class="title">
					{{seller.name}}
				</div>
				<m-star  :score = 'seller.score' ></m-star>
			<span class="rating">
				({{seller.ratingCount}})
			</span>
			<span class="sell">
				月售{{seller.sellCount}}单
			</span>
              <div class="like"><i class="icon iconfont icon-like"  @click="change()" id="like"><div class="shoucang">{{msg}}</div></i></div>
               
			</div>
	    <div class="content2">
	    	 <div class="tab-item">
                <span class="tab-descp">起送价</span><br>
                 <span class="tab-descp2">{{seller.minPrice}}</span>
                 <span class="tab-descp3">元</span>
            </div>
              
              <div class="tab-item">
                <span class="tab-descp">商家配送</span><br>
                 <span class="tab-descp2" >{{seller.deliveryPrice}}</span>
                 <span class="tab-descp3">元</span>
            </div>
               <div class="tab-item">
                <span class="tab-descp">平均配送时间</span><br>
                 <span class="tab-descp2">{{seller.deliveryTime}}</span>
                 <span class="tab-descp3">元</span>
            </div>

	    </div>
	    
      <!--  <hr class="hr-1"> -->

            <div class="white">
            	    
            </div>
            <div class="content3">
                <span class="title3">
                	 公告与活动
                </span>
                <p>
                 <div class="bulletin">
                 	 {{seller.bulletin}}	
                 </div>
                </p>

                <ul class="list" v-if='seller.supports'>
                     <li>
                     	<img src="../../../static/imgs/decrease.png"  alt="" class="img1">
                     	<span>{{seller.supports[0].description}} </span>
                     </li> 
                     <li>
                     	<img src="../../../static/imgs/discount.png"   alt="" class="img1" >
                     	<span>{{seller.supports[2].description}} </span>
                     </li>  
                     <li>
                     	<img src="../../../static/imgs/discount.png"   alt="" class="img1">
                     	<span>{{seller.supports[1].description}} </span>
                     </li> 
                     
                        <li>
                     	<img src="../../../static/imgs/special.png"   alt="" class="img1">
                     	<span>{{seller.supports[1].description}} </span>
                     </li>
                      <li>
                      	<img src="../../../static/imgs/special.png"   alt="" class="img1">
                     	<span>{{seller.supports[1].description}} </span>
                      </li>

                      <li>
                      	 <img src="../../../static/imgs/invoice.png"   alt="" class="img1" >
                     	<span>{{seller.supports[3].description}} </span>
                      </li> 
                       <li>
                      	 <img src="../../../static/imgs/guarantee.png"   alt=""  class="img1">
                     	<span>{{seller.supports[4].description}} </span>
                      </li> 
                          	
                </ul>
        	
            </div>
            <div class="white2"></div>
            <div class="content4">
                <span class="title4" >商家实景</span>
                <div class="content4-1" ref = "menuWrapper1" >
                    <ul v-if='seller.pics' ref = 'pics'>
                        <li v-for="(v,k) in seller.pics"> 
                            <img  :src="v" width="120" height="80">
                        </li>
                    </ul> 
                </div>
            </div>
            <div class="white3"></div>
            <div class="content5">
                <span class="title5">商家信息</span>
                <ul>
                    <li v-for="(v,k) in seller.infos">{{v}}</li>
                </ul>
            </div>
		</div>	
	</div>
</template>

<script>

    import {mapGetters} from 'vuex'
    import mStar from '../star/star.vue'

    export default {
        computed:{
            ...mapGetters([
                'seller'
            ]),
            msg(){
                if(!this.on){
                return "已收藏";
            }
                else{
                    return "收藏";
                }
            }
        },
       data(){
            return {
                on : true
                
                
            }
        },
        created(){
            this.$store.dispatch('getSeller');
                this.$nextTick( () => {
                setTimeout( () => {
                    this._initMenu();
                    this._initseller();
                },100);
            })
        },
        components:{
            'm-star':mStar
        },

        methods:{

            change(){
                console.log("5");
                this.on=!this.on;
                if(!this.on){
                    document.getElementById("like").style.color="red";
                    
                    
                }
                else{
                    document.getElementById("like").style.color="gray";
                    
                    
                }
            },
            _initMenu(){
                this.menuBS = new BScroll(this.$refs.menuWrapper,{
                    click:true
                });
            },

            _initseller(){
                let margin = 4;
                let width  = 120;
                this.$refs.pics.style.width = ( width + margin ) * this.seller.pics.length - margin + 'px';
                this.sellerBS = new BScroll(this.$refs.menuWrapper1,{
                    scrollX : true,
                    scrollY : 0,
                    click:true
                });
            }
        }
    }	
</script>

<style lang='less' scoped>
.sellers{
    position: absolute;
    z-index:-10;
    top:3.48rem;
    bottom:0.92rem;
    width:100%;	
    overflow: hidden;
    .content{
        top:0.36rem;
        left:0.36rem;
        right:0.36rem;
        padding-bottom: 0.36rem;
        // position: absolute;
        border-bottom: 1px solid rgba(7,17,27,0.1) ;
        .title{
            font-size: 0.3rem;
            color: rgb(7,17,27);
            line-height: 0.28rem;
            margin-top: 0.36rem;
            margin-left: 0.36rem;
        }
        .star {
            position: relative;
            top:0.5rem;
            margin-left:0.36rem;
        }
        .rating{
            position: relative;
            top:0rem;
            font-size: 0.3rem;
            color:rgb(77,85,93);
            left:3.5rem;
        }
        .sell{
            color:rgb(77,85,93);
            position: relative;
            left:3.7rem;
            font-size: 0.3rem;
            line-height: 0.36rem;
            top:0.02rem;
        }
        .like{
          position: absolute;
          right:0.36rem;
          text-align: center;
          top:0.23rem;
          
          width:1rem;

          i{
            font-size: 0.5rem;
            color: gray;
            
            .shoucang{
              
              margin-top: 0.16rem;
              font-size: 0.28rem;
              color:rgb(77,85,93);
              }
          }
         

        }
         
    }
    .content2{
            top:1.7rem;
            left:0.36rem;
            right:0.36rem;
            padding-bottom: 0.36rem;
            padding-top: 0.36rem;
            display: flex;       	
            .tab-item{
                    flex:1;
                    text-align: center;
                .tab-descp{
                    font-size: 0.2rem;
                    color: rgb(147,153,159);
                    line-height: 0.3rem;
                }
                .tab-descp2{
                    font-size: 0.48rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                    line-height: 0.48rem;
                }
                .tab-descp3{
                    font-size: 0.2rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                    line-height: 0.48rem;
                }
            }
    }
    .white{
        width: 100%;
        background: #f3f5f7;
        position: relative;
        top:0rem;
        height:0.4rem;
        border-bottom: 2px solid rgba(7,17,27,0.1);
        border-top:2px solid  rgba(7,17,27,0.1);
    }
    .content3{
        top:3.6rem;
        left:0.36rem;
        right:0.36rem;
        padding-bottom: 0.2rem;
        padding-top: 0.36rem;
        margin-left: 0.36rem;
        margin-right: 0.36rem;
        .title3{
            font-size:0.3rem;
            font-weight: bold;
        }
        .bulletin{
            margin-top: 0.16rem;
            font-size: 0.14rem;
            font-weight: 200;
            color:rgb(240,20,20);
            line-height: 0.4rem;
        }
        ul{
            width: 100%;
            margin-top: 0.2rem;
            li{  
                border-top:1px solid rgba(7,17,27,0.1);
                padding-top:0.32rem;
                padding-bottom: 0.32rem;
                padding-left: 0.24rem; 
                .img1{
                    width: 0.34rem;
                    height:0.34rem;
                    vertical-align: middle;
                }
            }
        }
    }
    .white2{
        width: 100%;
        background: #f3f5f7;
        position: relative;
        top:0rem;
        height:0.4rem;
        border-bottom: 2px solid rgba(7,17,27,0.1);
        border-top:2px solid  rgba(7,17,27,0.1);
    }
    .content4{
        top:3.6rem;
        left:0.36rem;
        right:0.36rem;
        padding-bottom: 0.36rem;
        padding-top: 0.36rem;
        margin-left: 0.36rem;
        margin-right: 0.36rem;
        .title4{
            font-size:0.3rem;
            font-weight: bold;
        }
        .content4-1{
            margin-top: 0.24rem;
            height: 1.8rem;
            overflow: hidden;
            
            width:6.8rem;
            ul{         
                
                list-style: none;
                display:flex;
                li{
                    margin-right:0.04rem;
                    
                    img{
                        height: 1.8rem;
                        width: 2.4rem;  
                    }
                }                       		
            } 
        }
    }
    .white3{
        width: 100%;
        background: #f3f5f7;
        position: relative;
        top:0rem;
        height:0.4rem;
        border-bottom: 2px solid rgba(7,17,27,0.1);
        border-top:2px solid  rgba(7,17,27,0.1);
    }
    .content5{
        top:3.6rem;
        left:0.36rem;
        right:0.36rem;
        padding-bottom: 0.36rem;
        padding-top: 0.36rem;
        margin-left: 0.36rem;
        margin-right: 0.36rem;
        .title5{
            font-size:0.3rem;
            font-weight: bold;
        }
        ul{
            margin-top: 0.24rem;
            li{
                padding:0.32rem  0.24rem;
                border-top:1px solid  rgba(7,17,27,0.1);
                font-size: 0.24rem;
                font-weight: 200;
                color: rgb(7,17,27);
            }
        }
    }
}
</style>


