<template>
    <div class="shopcar">	     	
        <div class="footer">
            <div class="num" v-show="totalPrice > 0">{{totalCount}}</div>
            <div class="outCircle" @click = 'ShowMore'>
                <div class="inCircle" :class='{"enough3":totalPrice > 0}'>
                    <span class="icon-shopping_cart">
                        <i class="icon iconfont icon-gouwuche"></i>
                    </span>
                </div>
            </div>
            <div class="footerLeft":class='{"enough2":totalPrice > 0}'>&yen;{{totalPrice}}</div>
            <div class="footerMid">另需配送费&yen;4元</div>
            <div class="footerRignt":class='{"enough":totalPrice >= seller.minPrice}'>{{payDesc}}</div>
        </div>
        <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown" >
            <div class="showmore" v-show = 'listshow'  >
            <!-- <div class="showmore" v-if = 'onoff && totalPrice > 0'  > -->
                <div class="title">
                    <div class="title-left">购物车</div>
                    <div class="title-right" @click="empty">清空</div>
                </div >

                <div class='foodDetail' ref = "menuWrapper" > 
                    <ul  >
                        <li v-for = 'item in selectFoods'  >
                            <span class="name">{{item.name}}</span>
                            <span class="price"><span class="yen">&yen;</span>{{item.price*item.count}}</span>
                            <span class="cartcon">
                                <Cartcontrol  :food='item' :totalCount='totalCount'></Cartcontrol>
                            </span>  
                        </li>
                    </ul>
                </div> 
            </div>
       </transition> 
        <div class="back" v-show = 'listshow' @click = 'close'></div>

    </div>   
</template>

<script>	
    import {mapGetters} from 'vuex'
    import Cartcontrol from '../Cartcontrol/Cartcontrol.vue'
    import Vue from 'vue'
    export default {
        data(){
            return {
                fold : true
            }
        },
        methods:{
            ShowMore(){
                if( !this.totalCount ){
                    return;
                }
                this.fold = !this.fold;
            },
            close(){
                this.fold = !this.fold;
            },
            empty(){
                this.selectFoods.forEach(food => {
                    food.count=0;    
                });
               // this.onoff=false;
                this.fold = true;
            },
        },
        components : {
            Cartcontrol
        },
        computed:{
            ...mapGetters([
                'seller',
                'goods',
                'food'
            ]),
            listshow(){
                // 状态初始化重置
                if(this.totalCount==0){
                    this.fold = true;
                }
                if( !this.totalCount ){
                    return;
                }
                let show = !this.fold;
                if( show ){
                    this.$nextTick( () => {
                        if( !this.cc ){
                            this.cc = new BScroll(this.$refs.menuWrapper,{
                                click:true
                             });
                        }else{
                            this.cc.refresh();
                        }
                    });
                }
                return show ;
            },
            selectFoods(){
                let foods = [];
                this.goods.forEach( good => {
                    good.foods.forEach( food => {
                        if (food.count > 0){
                            foods.push(food);
                        }
                    }) 
                })

                return foods;
            },
            totalPrice(){
                let total = 0;
                this.selectFoods.forEach(food => {
                    total += food.price * food.count;
                });
                return total;
            },
            totalCount(){
                let count = 0;
                this.selectFoods.forEach(food => {
                    count += food.count;
                });
                return count;
            },
            payDesc(){
                if(this.totalPrice === 0 ){
                    return `￥${this.seller.minPrice}元起送`
                }else if( this.totalPrice < this.seller.minPrice ){
                    return `还差${this.seller.minPrice - this.totalPrice}元起送`
                }else{
                    return "去结算"
                }
            }
        }
    };

</script>


<style lang="less" scoped>
.shopcar{

    .showmore{
        width: 100%;
        background: white;
        bottom: 1rem;
        z-index: 2;
        position: absolute;
        max-height: 4.9rem;
        overflow: hidden;
        .title{
            display: flex;
            justify-content:space-between;
            height: 0.8rem;
            background: #f3f5f7;
            .title-left{
                margin-left: 0.36rem;
                
                font-size: 0.28rem;
                font-weight: 200;
                color: rgb(7,17,27);
                line-height: 0.8rem;
            }
            .title-right{
                font-size: 0.24rem;
                color:rgb(0,160,220);
                line-height: 0.8rem;
                margin-right: 0.36rem;
            }

        }
        .foodDetail{ 
            width: 100%;
            max-height: 4.6rem;
            overflow: hidden;
            li{
                width: 100%;
                height: 0.96rem;
                    border-top: 1px solid rgba(7,17,27,0.1);
                    .name{
                        margin-left: 0.36rem;
                        font-size: 0.28rem;
                        line-height: 0.96rem;
                        color: rgb(7,17,27);
                        font-weight: bold;
                    }
                    .price{
                        position: absolute;
                        right:2.5rem;
                        font-size:0.28rem;
                        font-weight: normal/700;
                        color: rgb(240,20,20);
                        line-height: 0.96rem; 
                        .yen{
                            font-size: 0.24rem;
                            margin-right: 0.1rem;
                        }
                    }
                    .cartcon{
                       position: absolute;
                       right:0.4rem;
                       margin-top: 0.22rem; 
                    }
            }
        }
    }
    .back{
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 0;
        position: absolute;
        background-color:rgba(7,17,27,0.6);
        
    }
    .footer{
        z-index: 99;
        width: 100%;
        height: 1.1rem;
        background-color:#1d1427;
        position:fixed;
        bottom:0;
        .num{
            width:0.46rem;
            height:0.29rem;
            background: red;
            color: white;
            position: fixed;
            z-index: 5;
            left:1.05rem;
            bottom: 1rem;
            border-radius: 0.16rem;
            line-height: 0.36rem;
            text-align: center;
        }
        .outCircle{
            width: 1.12rem;
            height:1.12rem;
            border-radius: 0.56rem;
            position: absolute;
            left:0.36rem;
            bottom:0.16rem;
            background-color:#1d1427;
            .inCircle{
                width: 0.88rem;
                height:0.88rem;
                border-radius: 0.44rem;
                position: absolute;
                left:0.12rem;
                bottom:0.12rem;
                background-color:rgba(255,255,255,0.2);
                color:rgba(255,255,255,0.4);
                font-size: 0.52rem;
                text-align: center;
                line-height: 0.88rem;
                &.enough3{
                     background:rgb(0,160,220);
                   color: white; 
                  // color:rgb(0,160,220);
                }
                i{
                    font-size: 0.47rem;
                }
            }
        };
        .footerLeft{
            
            font-size: 0.32rem;
            color:rgba(255,255,255,0.4);
            margin-left: 1.6rem;
            margin-top: 0.24rem;
            margin-bottom: 0.24rem;
            line-height: 0.48rem;
            padding-right: 0.24rem;
            border-right: 0.02rem solid rgba(255,255,255,0.1);
            float:left;
            &.enough2{
                color: white;

            }
        };
        .footerMid{
            font-size: 0.24rem;
            color:rgba(255,255,255,0.4);
            margin-left: 0.24rem;
            margin-top: 0.24rem;
            margin-bottom: 0.24rem;
            line-height: 0.52rem;
            float:left;
        };
        .footerRignt{
            width:2.10rem;
            height: 1.1rem;
            float:right;
            background-color:rgba(255,255,255,0.2);
            color:rgba(255,255,255,0.4);
            font-size: 0.32rem;
            text-align: center;
            line-height: 1.1rem;
            
            &.enough{
                background:rgb(0,160,220);
                color: #fff; 
                    
            } 
        }
    }
}
	
</style>


