<template>
  <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown" >
  	 <div class="detail" v-show = 'detailshow'>
  		  <div class="title">{{seller.name}}</div>	
  		  <el-rate v-model="seller.score"  disabled show-text text-color="#ff9900" text-template="{value}" class="star"></el-rate>		
  		  <hr class="hr-1">
  		  <div class="title2">优惠信息</div> 
  		  <hr class="hr-2">
        <div>     	   
  		    <ul class="pics">			
  			     <li ><img src="../../../static/imgs/decrease.png"  alt=""></li>
  			     <li> <img src="../../../static/imgs/discount.png" alt=""></li>			     
             <li> <img src="../../../static/imgs/discount.png"  alt=""></li>
             <li> <img src="../../../static/imgs/special.png" alt=""></li>
  			     <li> <img src="../../../static/imgs/special.png"  alt=""></li>	             
  		    </ul>
  		    <ul v-if='seller.supports' class="desp">
  			  <li v-for="(v,k) in seller.supports">{{v.description}}</li>
  		    </ul> 
       </div>
        <hr class="hr-3">
  		  <div class="title3">商家公告</div> 
  		  <hr class="hr-4">
  		  <div class="bulletin">			
  				{{seller.bulletin}}			
  		  </div>        
  		  <div class="icon2" >
  		  <i class="el-icon-close "  @click = 'close' ></i>		 
        </div> 		
  	</div>
  </transition>
</template>

<script>
	import {mapGetters} from 'vuex'
  import '_animate.css@3.5.2@animate.css/animate.css'
	export default {
		computed : {
			...mapGetters([
				'detailshow',
				'seller'
			])
		},
		methods : {
			close(){
				this.$store.dispatch('closedetail')
			}
		}
		
	}
</script>

<style lang='less' scoped>
	.detail{
		  position: fixed;         
		  top:0;
		  left: 0;
      width: 100%;
      height: 100%;
      z-index: 999;
		  background:rgba(7,17,27,0.8);
		  color:rgb(255,255,255);		
		  .title{						
      margin:0 auto;
      margin-top:1.28rem; 
      text-align: center;
			font-size:0.34rem;
			font-weight: 700;
			line-height: 0.32rem;
			position: relative;
			width: 4rem;			
		  }
      .star{
    	position: relative;
    	margin:0 auto;
    	margin-top: 0.4rem;
    	text-align: center;
    	width: 4rem;
    	height:0.48rem;
      }
      .hr-1{
     	width:2.1rem;
     	margin-top: 0.56rem;
     	margin-left: 0.7rem;
     	display: inline-block;       
     } 
     .title2{
     	display: inline-block;
     	font-size: 0.28rem;
     	font-weight: bold;
     	margin-left: 0.23rem;
     	margin-right: 0.23rem;
     }
      .hr-2{
    	width:2.1rem;
    	margin-top:0.56rem;
    	margin-right:0.7rem;
    	display: inline-block;
      }
      .pics{
      	margin-left: 0.72rem;
      	margin-top: 0.48rem;
      	display: inline-block;
      	img{
      		width:0.34rem;
      		height:0.34rem;
      		margin-bottom: 0.24rem;
      	}
      }
      .desp{
         display: inline-block;
         position: absolute;
         margin-top: 0.55rem;
         margin-left: 0.12rem;

         li{
         	margin-bottom: 0.38rem;
         	font-size: 0.27rem;
         	font-weight: 200;
         	line-height: 0.24rem;
         }
      }

        .hr-3{
        width:2.1rem;
       	margin-top: 0.56rem;
       	margin-left: 0.7rem;
       	display: inline-block;
        }
        .title3{

        display: inline-block;
       	font-size: 0.28rem;
       	font-weight: bold;
       	margin-left: 0.23rem;
       	margin-right: 0.23rem;
        }
        .hr-4{
        	width:2.1rem;
        	margin-top:0.56rem;
        	margin-right:0.7rem;
        	display: inline-block;
        }
        .bulletin{
        	position: relative;
        	margin:0 auto;
            text-indent:0.5rem;
            padding:0  0.96rem; 
        	font-size: 0.24rem;
        	font-weight: 200;
        	line-height: 0.48rem;
        	margin-top: 0.48rem;
        }
        




        .icon2{
       
       	margin:0 auto;
       	margin-top: 1.4rem;
       	bottom:2rem;
       	text-align:center;
       	width:4rem; 
       	height:1rem;
       	height:0.8rem;
       	font-size: 0.64rem;
       	color: rgba(255,255,255,0.5);
        z-index: 99; 
        i{
          display: block;
        } 
       } 

	}
</style>


