<template>
	 <div class="header">
        <div class="background">
			<img :src="seller.avatar">
		</div>
	 	<div class="header-top">
 			<img class="avatar" :src="seller.avatar" alt="" @click='show'>
	 		<div class="content">
	 			<p class="title">
	 				<img src="../../../static/imgs/brand.png" class="brand">{{seller.name}}</p>
	 			<p class="description">{{seller.description}}&nbsp /&nbsp {{seller.deliveryTime}}分钟送达</p>
                <p class="support1" v-if='seller.supports'>
                	<img src="../../../static/imgs/decrease.png" class="decrease" >
                	{{seller.supports[0].description}}
                </p>	 			
	 		</div>
	 	</div>
		<div class="header-bottom">
			<img src="../../../static/imgs/bulletin.png"  alt="" class="bottom-pic">
			<span class="bulletin" >{{seller.bulletin}}</span> 
			<i class="icon iconfont icon-jiankuohaocudayou"></i> 
		</div>
		<div class="right" @click='show' v-if='seller.supports'>
		    {{seller.supports.length}}个&nbsp;> 
		</div>
     <Detail> </Detail>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex'
	import Detail from '../detail/Detail.vue'

	export  default {
		computed :{
			...mapGetters([
				'seller',
				'goods'
			])
		},
		components:{
			Detail
		},
		created(){
          this.$store.dispatch('getSeller');
		},
		methods:{
			show(){
				this.$store.dispatch('showdetail');
			}
		}
	}
</script>

<style lang="less" scoped>
		.header{
			height: 2.68rem;
			background:rgba(7,17,27,0.5);
			color:rgb(255,255,255);
			position: absolute;
		
			width: 100%;
			overflow: hidden;
			.background{
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				z-index: -10;

				filter:blur(5px);
				img{ 
					width: 100%;
					height: 100%;
				}
			}   
			.header-top{
				z-index: -10;
				height: 1.8rem;
				padding: 0.48rem 0.24rem 0.36rem 0.48rem;
				.avatar{
					width:1.28rem;
					height: 1.28rem;
					display: inline-block;
				}
				.content{
					display: inline-block;
					margin-left: 0.32rem;
				    padding:0.04rem;                    
					.title{
                        font-size: 0.32rem;
                        padding-bottom: 0.16rem;
                        font-weight: bold;                    
						.brand{
							vertical-align: middle;
							width: 0.68rem;
							height: 0.41rem;
							padding-right:0.12rem;
							border-radius: 0.04rem;
						}
					}
					.description{
						font-size: 0.24rem;
						font-weight: 200;
						line-height: 0.24rem;
						padding-bottom: 0.2rem;
					}
					.support1{
                         font-size: 0.2rem;
						.decrease{
							vertical-align: middle;
							width: 0.38rem;
							height:0.35rem;
							padding-right:0.08rem;
						}
					}

				}
			}
			.header-bottom{
             margin-top:-0.55rem;
             height:0.38rem;             
             background-color: rgba(7,17,27,0.2);
             padding: 0.1rem 0.24rem 0.1rem 0.24rem;
                .bottom-pic{
                  position: absolute;
                  width: 0.48rem;
                  margin-top: 0.08rem;
				  height: 0.3rem;
                }
              .bulletin{
              	 position: absolute;
              	 display: inline-block;
                 font-size: 0.2rem;
                 font-weight: 200;
                 line-height: 0.45rem;
                 height:0.4rem;
                 width:6.4rem;
   				 margin-left: 0.6rem;
                 overflow: hidden;
                 white-space: nowrap;
                 text-overflow:ellipsis;
                } 
                .icon{
                position: absolute;
                font-size: 0.07rem;
                right:7px;
                display: inline-block;
                line-height: 0.45rem;	
                }
			}
			.right{
				position: absolute;
				font-size: 0.21rem;
				background:rgba(0,0,0,0.2);
				top:1.35rem;
				right:0.24rem;
				border-radius:0.3rem;
				width:1rem;
				text-align: center;
				height:0.48rem;
				line-height: 0.48rem;
			}
		}
	 
</style>


