<template>
	<div class="goods">
		<div class="goods-menu" ref = "menuWrapper"  >       
				<ul>
					<li v-for="(v,index) in goods" class="menu-list"  @click='selectMenu(index,$event)' :class = "{'current' : index === currentIndex}" >
					 <span class="text">{{v.name}}</span>
				   </li>							
			   </ul>					
		 </div>
		<div class="goods-foods" ref = "foodsWrapper" >
			<ul >
				<li v-for="v in goods" class="food-list food-list-hook">
                    <span class="v-name" >{{v.name}}</span> 
					<ul>
						<li v-for="food in v.foods" class="food" @click = 'showfood(food,$event)'>
						    <img :src="food.icon" alt="" class="foodicon">
							<div class="foodinfo">
								<p class="foodname">{{food.name}}</p>
								<p class="fooddes">{{food.description}}</p>
								<p class="foodcount">月售{{food.sellCount}}份&nbsp;好评率{{food.rating}}%</p>
								<div class="foodprice">
									<p><span class="yen">&yen;</span>{{food.price}}</p>

								</div>
							</div>
							<div class="cart">
								<Cartcontrol  :food='food'></Cartcontrol>		
							</div>						  						
						</li>
					</ul>
				</li>
			</ul>
		</div>
		<foodDetail></foodDetail>
	</div>
</template>

<script>
import {mapGetters} from 'vuex'
import foodDetail from '../detail/foodDetail.vue'
import Footer from '../footer/footer.vue'
import Cartcontrol from '../Cartcontrol/Cartcontrol.vue'

export default {

		data(){
			return {
				listHeight : [],
				scrollY : 0
			}
		},
		created(){
			this.$store.dispatch('getGoods');
			this.$nextTick( () => {
				console.log(this.calcHeight)
				setTimeout(() => {
					this._initMenu();
					this._initFood();
					this._calcHeight();
				},100)
			})
		},
		components:{
	    	Cartcontrol,	    	
	     	foodDetail
        },
		computed:{
            ...mapGetters([
				'goods',
				'foodshow',
				'food'
				
		    ]),
		    currentIndex(){
		    	for( let i = 0 ; i < this.listHeight.length ; i++ ){
		    		let height1 = this.listHeight[i];
		    		let height2 = this.listHeight[i+1];
		    		if( this.scrollY >= height1 && this.scrollY < height2 ){
		    			return i;
		    		}
		    	}
		    	return 0;
		    },

		},

		methods : {
			showfood(f,e){
				if( !e._constructed ){
					return;
				};
				console.log(4)
				this.$store.dispatch('showfood',f);
			},
			selectMenu(key,e){
				if( !e._constructed ){
					return;
				}
            	let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
				let el = foodList[key];
				this.foodBS.scrollToElement(el,300);

			},
            _calcHeight(){
            	let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
            	let height = 0;
            	this.listHeight.push(height);
            	for(let i = 0 ; i < foodList.length ; i++){
            		height += foodList[i].clientHeight;
            		this.listHeight.push(height);
            	}
            	console.log(this.listHeight);
            },
			_initMenu(){
				this.menuBS = new BScroll(this.$refs.menuWrapper,{click:true});
			},
			_initFood(){
				
				this.foodBS = new BScroll(this.$refs.foodsWrapper,{
					probeType : 3,
					click : true
				});
				this.foodBS.on('scroll',(pos) =>{
					this.scrollY = Math.abs(Math.round(pos.y));
					console.log(this.scrollY);
				})
			},
		},

}	
</script>

<style lang='less' scoped>
	.goods{
		position: absolute;
		top: 3.48rem;
		bottom: 1.1rem;
		width: 100%;
		display: flex;
		overflow: hidden;
		.goods-menu{
			flex:0 0 1.6rem;
			width: 1.6rem;
            
            font-size: 0.24rem;
            
            font-weight: 200;
          
            li{  
            	 
            	&.current {
					border-right:2px solid #d9dde1; 

				}
                border-bottom:1px solid rgba(7,17,27,0.1);

                margin:0 0.24rem;
                display: table; 
            	height:1.08rem;            	
            	text-align: center;

            	.text{
            		text-align: center;
            		display:table-cell;
            		width: 1.12rem;
            		vertical-align: middle;
            	}
            }


		}
		.goods-foods{
			overflow: hidden;
			flex:1;
			.v-name{
				font-size: 0.24rem;
				padding-left: 0.28rem;
				line-height: 0.52rem;
				color: rgb(147,153,159);
			}
			.food{
				display: flex;
				border-bottom: 0.02rem solid rgba(7,17,27,0.1);
				margin: 0.36rem;
				padding-bottom: 0.36rem;
				.foodicon{
					width:1.3rem;
					height: 1.3rem;
				}
				.foodinfo{
					margin-left: 0.2rem;
					.foodname{
						font-size: 0.28rem;
						line-height: 0.28rem;
						padding-top: 0.04rem;
					}
					.fooddes{
						font-size: 0.2rem;
						line-height: 0.3rem;
						padding-top: 0.16rem;
						color:rgb(147,153,159);
					}
					.foodcount{
						font-size: 0.20rem;
						line-height: 0.20rem;
						padding-top: 0.16rem;
						color:rgb(147,153,159);
					}
					.foodprice{
						width:3.5rem;
						font-size: 0.3rem;
						line-height: 0.28rem;
						padding-top: 0.16rem;
						color:red;
						display: flex;
						justify-content:space-between;
						.yen{
						  font-size: 0.20rem;	
						}
					}
				}
				.cart{
					position: relative;
					right:1.2rem;
					top:1.1rem;
				}
			}

		}
	}
</style>



