<template>
    <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown" >
		<div class="goodsdetail" v-show="foodshow"   ref = "menuWrapper" >
			<div style="height:600px;" >
 				<div class="detail-content">
					<div class="image-header">
						<img :src="food.image" alt="">
					</div>
					<div class="return" @click = 'hidefood'>
						 
						 <i class="icon iconfont icon-fanhui"></i>
					</div>
				</div>
				<div class="content1">
					<p class="title-food">{{food.name}}</p>
					<p class="title-desc">{{food.description}}</p>
					<p>
						<span class="title-count">月售{{food.sellCount}}份</span>
						<span class="title-rating">&nbsp;好评率{{food.rating}}%</span> 
					</p>
					<p class="title-price" >
					     <span class="yen">&yen;</span>
					     {{food.price}}
					</p> 				
					<div class="buy" @click="add" v-show="!food.count">加入购物车
					</div>
					<div class="show"> 
						<Cartcontrol  :food='food'>						
						</Cartcontrol>
					</div>									
				</div>
				<div class="white1" v-if='food.info'>					
				</div>
				<div class="content2" v-if='food.info'>
				  	<p class="title2">商品介绍</p>
				  	<p class="foodinfo">{{food.info}}</p>
				</div>
				<div class="white2"></div>
				<div class="content3">
					<p class="title3">商品评价</p>
					<ul v-if="food.ratings">
				    	 <li class="li1" @click="getAll" :class="{'select1':yes===-1}">全部&nbsp;
				    	    <span  class="number">{{food.ratings.length}}</span>
				    	 </li>
				    	 <li class="li2" @click="ok" :class="{'select2':yes===0}">推荐&nbsp;
				    	 	<span class="number"> {{ countnum}}</span>
				    	 </li>
				    	 <li class="li3" @click="no" :class="{'select2':yes===1}">吐槽&nbsp;
				    	 	<span class="number">{{food.ratings.length-countnum}}</span>
				    	 </li>
					
					</ul> 
					<div class="icon2" @click="changeFlag" >
	                      <i class="icon iconfont icon-check" :class="{'checked':flag}" ></i>
	                      &nbsp;
	                      <span class="see" >只看有内容的评价</span>
	                </div>  
				</div>
				<div class="line">
					
				</div>
				<div class="evaluate">
                        <ul>
                            <li v-for="(v,k) in foodList" :key='k' class="menu-list" v-show="all || yes === v.rateType">
                                <ul class="ev_con">
                                    <li class="left">
                                        <p>{{v.rateTime | date}}</p>
                                        <p>
                                            <span v-if="v.rateType == 0" class="good"><i class="iconfont icon-damuzhion"></i></span>
                                            <span v-else="v.rateType == 1" class="bad"><i class="iconfont icon-down"></i></span>&nbsp;&nbsp;
                                            <span class="text">{{v.text}}</span>
                                        </p>
                                    </li>
                                    <li class="right">
                                        <ul class="r_con">
                                            <li class="username">{{v.username}}</li>
                                            <li><img :src="v.avatar" class='pic'></li>
                                        </ul>
                                    </li>
                                </ul>
                            </li>
                        </ul>
                    </div>
				
	
			</div>
		</div>
    </transition>
</template>

<script>
	import {mapGetters} from 'vuex'
	import '_animate.css@3.5.2@animate.css/animate.css'
  	import Cartcontrol from '../Cartcontrol/Cartcontrol.vue'
  	import Vue from 'vue'

	export default {
		data() {
            return {
                all: true,
                yes: -1,
                flag: false
            }
        },
		computed:{
			...mapGetters([
				'foodshow',
				'food'
			]),
			  foodList() {
                if (this.flag) {
                    return this.food.ratings.filter((item) => {
                        return item.text;
                    });
                }
                return this.food.ratings;
            },
             countnum(){
                return this.food.ratings.filter((item) => {
                        return item.rateType === 0
                    }).length;
            }
		},
		filters:{
            date:function(value){
                function toDouble (n){
                    return n<10?'0'+n:n;
                }
                var d = new Date(value);
                return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
            }
        },
		components:{
	    	Cartcontrol	    	
	     	
        },
		methods:{
			hidefood(){
				
				this.$store.dispatch('hidefood');
			},
			changeFlag() {
                this.flag = !this.flag;
                
            },
            refresh() {
                setTimeout(() => {
                    this.menuBS.refresh();
                }, 20)
            },
            ok() {
                this.yes = 0;
                this.all = false;
            },
            no() {
                this.yes = 1;
                this.all = false;
            },
             getAll() {
                this.yes = -1;
                this.all = true;
            },
			 _initMenu(){
                this.menuBS = new BScroll(this.$refs.menuWrapper,{
                 	click:true
                });
            },
            add(){
				Vue.set(this.food , 'count' ,1);
			}
		},
		created(){
                this.$store.dispatch('getGoods');
                this.$nextTick( () => {
                setTimeout( () => {
                    this._initMenu();
                },100);
            })
        },
	}
</script>

<style lang="less" scoped>
	.goodsdetail{
		background-color: white;
		position:fixed;
		top: 0;
		right:0;
		bottom:0.92rem;
		left: 0;
		overflow: hidden;
		width: 100%;
		
		.detail-content{
            

			.image-header{
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom: 100%;
				img{
					position: absolute;
					width: 100%;
					height: 100%;
					left: 0;
					top:0;
				}
			}
			.return{
				position: fixed;
				top:0.4rem;
				left: 0.4rem;
				padding:0.2rem;
				font-size:23px;				
				color: white;
			}			
		}
		.content1{    
        	margin-top: 0.36rem;
        	margin-left:0.36rem;
        	margin-right:0.36rem;
        	.title-food{
        		font-size:0.3rem;
        		font-weight: 700;
        		color:rgb(7,17,27);
        		line-height: 0.28rem;
        		margin-bottom: 0.16rem;
        	}
        	.title-desc{
        		font-size:0.20rem;
        		color:rgb(147,153,159);
        		line-height: 0.2rem;
        		margin-bottom: 0.16rem;
        	}
        	.title-count{
        		font-size:0.20rem;
        		color:rgb(147,153,159);
        		line-height: 0.2rem;
        	}
        	.title-rating{        		
        		font-size:0.20rem;
        		color:rgb(147,153,159);
        		line-height: 0.2rem;
        	}
        	.title-price{
        		font-size:0.35rem;
        		font-weight: normal/700;
        		color:rgb(240,20,20);
        		line-height: 0.48rem;
        		margin-top: 0.2rem;
        		.yen{
        			font-size: 0.2rem;
        		}                 
        	}
        	.buy{
        		position: absolute;
        		width:1.48rem;
        		height:0.48rem;
        		border-radius: 0.24rem;
        		line-height: 0.48rem;
        		text-align: center;
        		right:0.36rem;
        		top:8.8rem;
        		color: white;
        		background:rgb(0,160,200);
        		z-index: 5;
        	}
        	.show{
        		position: absolute;
        		right:0.36rem;
        		top:8.8rem;

        	}

		}
			.white1{
  				width: 100%;
		        background: #f3f5f7;
		        /* position: relative;
		        top:0rem; */
		        margin-top: 0.3rem;
		        height:0.4rem;
		        border-bottom: 2px solid rgba(7,17,27,0.1);
		        border-top:2px solid  rgba(7,17,27,0.1);
		}
		.content2{
			    margin-top: 0.36rem;
		        margin-left:0.36rem;
		        margin-right:0.36rem;
		        	.title2{
		        		font-size: 0.3rem;
		        		font-weight:700;
		        	}
		        	.foodinfo{
		        		margin-top: 0.12rem;
		        		margin-left:0.16rem;
        		        font-weight: 200;
        		        line-height: 0.48rem;
        		        color: rgb(77,85,93);
		        	}
		}
		.white2{

           		width: 100%;
		        background: #f3f5f7;
		        /* position: relative;
		        top:0rem; */
		        margin-top: 0.3rem;
		        height:0.4rem;
		        border-bottom: 2px solid rgba(7,17,27,0.1);
		        border-top:2px solid  rgba(7,17,27,0.1);
		}
		.content3{
				margin-top: 0.36rem;
		        margin-left:0.36rem;
		        margin-right:0.36rem;
		        .title3{
		        	font-size: 0.3rem;
		        	font-weight:700; 
		        }
		        ul{
		        	list-style: none;
		        	display: flex;
		        	margin-top: 0.36rem;
		        	font-size: 0.24rem;
					border-bottom:1px solid rgba(7,17,27,0.1);
					padding-bottom: 0.36rem;  
		        	li{
		        		width: 1.2rem;
		        		height:0.58rem;
		        		color:rgb(7,17,27);
		        		line-height: 0.58rem;
		        		text-align: center;
		        		font-size: 0.26rem;
		        		
		        		margin-right: 0.16rem;
		        		border-radius: 0.1rem;
		        		.number{
		        			font-size: 0.2rem;
		        		}

		        	}
		        	.li1{
  						background: skyblue;
		        	}
		        	.select1{
		                background: rgb(0,160,220);
		                color:white;
                    }
		        	.li2{
		        		background: rgba(0,160,220,0.2);
		        	}
		        	 .select2{
		                background: rgb(0,160,220);
		                color:white;
                     } 

		        	.li3{
		        		background: gray;
		        	}
		        	.select3{
		                background: rgb(0,160,220);
		                color:white;
              		}

		        }
		        .icon2{
		        	margin-top: 0.24rem;
		        	color:gray;		        	
		        	.checked{
		        		color:rgb(0,160,220);
		        	}
		        }
		       

		}
		.line{
			width: 100%;
			background-color:rgba(7,17,27,0.1);
			height:2px;
			margin-top: 0.24rem;

		}
		     .evaluate{
                   margin: 0 0.36rem;
                   font-size: 0.2rem;
                   color:rgb(147,153,159);
                   line-height: 0.24rem;
                   .menu-list{
                       margin-top: 0.36rem;
                       .ev_con{
                        display: flex;
                        justify-content: space-between;
                        border-bottom:1px solid rgba(7, 17, 27, 0.1);
                        .left{
                            .good {
								color: rgb(0, 160, 220);
								font-size: 0.24rem;
								line-height: 0.84rem;
							}
							.bad {
								color: rgb(147, 153, 159);
								font-size: 0.24rem;
								line-height: 0.84rem;
							}
                            .text{
                                font-size: 0.24rem;
                                color:rgb(7,17,27);
                            }
                        }
                        .right{
                            .r_con{
                                display: flex;
                                .username{
                                	padding-top: 0.1rem;
                                }
                                .pic{
                                	margin-left: 0.12rem;
                                    width: 0.48rem;
                                    height: 0.48rem;
                                    border-radius: 0.4rem;

                                }
                            }
                        }
                    }
                }
            }



	}
</style>


