<template>
	<div class="ratings" ref = "menuWrapper">
    <div>
    
 
		<div class="content1">
			<div class="left">
				<ul>
					<li  class="score">
						{{seller.score}}
					</li>
					<li class="left1">
						综合评分
					</li>
					<li class="left2">
						高于周边商家{{seller.rankRate}}%
					</li>
				</ul>
			</div>
			<div class="right">
				 <ul>
				 	<li>
				 		<span class="right1">服务态度</span>
				 		
             <el-rate v-model="seller.serviceScore"  disabled  text-template="{value}" class="star">
             </el-rate>
				 	</li>
				 	<li>
				 		<span class="right1">商品评分</span>
				 		<el-rate v-model="seller.foodScore"  disabled  text-template="{value}" class="star">  </el-rate> 
                    
				 	</li>
				 	<li>
				 		<span class="right1">送达时间</span>
				 		<span class="right2"> {{seller.deliveryTime}}分钟</span>
				 		
				 	</li>
				 </ul>
			</div>
		</div>
		<div class="white1">
			
		</div>
    <div class="content2">
        
        <ul>
             <li class="li1" @click="getAll" :class="{'select1':yes===-1}">全部&nbsp;<span class="number" >{{ratings.length}}</span></li>
             <li class="li2" @click="ok" :class="{'select2':yes===0}">满意&nbsp;<span class="number">{{countnum}}</span></li>
             <li class="li3" @click="no" :class="{'select3':yes===1}">不满意&nbsp;<span class="number">{{ratings.length-countnum}}</span></li>        
        </ul>
        <div class="icon2"  @click="changeFlag">
             <i class="icon iconfont icon-check" :class="{'checked':flag}" ></i>
             <span class="see" >只看有内容的评价</span>
        </div>  
     </div>
     <div class="content3" style="margin-top:0.3rem;">
     
        <ul class="rating">
           <li v-for="(v,k) in ratLists" :key='k' class="menu-list" v-show="all || yes === v.rateType">
            <div class="rat">
              <img :src="v.avatar" alt="" class='pic'>
              <div class="ratR">
                <span>{{v.username}}</span>
               <span class="data">{{v.rateTime | date}}</span>
                <div class="star">
                    <el-rate v-model="v.score"  disabled  text-template="{value}" class="star1">
                    </el-rate>
                    <span class="time">{{v.deliveryTime}}分钟送达</span>
                </div>
                <p class="text">{{v.text}}</p>
                <div class="satisfy">
                    <span v-if="v.rateType == 0" class="good"> <i class="icon iconfont icon-damuzhion"></i></span>
                    <span v-else="v.rateType == 1" class="bad"><i class="icon iconfont icon-down"></i></span>
                    <div v-for="(v,k) in v.recommend" class="foods" v-if="k<3">
                        <span>{{v}}</span>
                    </div>
                </div>
              </div>
            </div>
          </li>
         </ul>   
     </div>
    </div>
	 </div>
</template>

<script>
    import {mapGetters} from 'vuex'
    import mStar from '../star/star.vue'
	export default {
      data() {
            return {
                all: true,
                yes: -1,
                flag: false
            }
        },
     computed:{
       	     ...mapGetters([
       	     	    'seller','ratings'
       	     	]),
             ratLists() {
                if (this.flag) {
                    return this.ratings.filter((item) => {
                        return item.text;
                    })
                }
                return this.ratings;
            },
            countnum(){
               return this.ratings.filter( (item) => {
                    return item.rateType===0
               }).length;
            }
       },
       filters:{
            date:function(value){
                function toDouble (n){
                    return n<10?'0'+n:n;
                }
                var d = new Date(value);
                return d.getFullYear()+"-"+toDouble(d.getMonth()+1)+"-"+toDouble(d.getDate())+" "+toDouble(d.getHours())+":"+toDouble(d.getMinutes());
            }
        },
    created(){
       	    this.$store.dispatch('getRatings');
            this.$nextTick( () => {
	          setTimeout( () => {
	          	this._initMenu();
	          },100)
      	 })
       },

    components:{
			'm-star':mStar
		},

    methods:{
		  _initMenu(){
			this.menuBS = new BScroll(this.$refs.menuWrapper,{
        click:true
      });
		},
      changeFlag() {
                this.flag = !this.flag
                this.refresh()
            },
      refresh() {
                setTimeout(() => {
                    this.menuBS.refresh();
                }, 20)
            },
      ok() {
                this.yes = 0
                this.all = false
                this.refresh();
            },
      no() {
                this.yes = 1
                this.all = false
                this.refresh()
            },
      getAll() {
        
                this.yes = -1
                this.all = true
                this.refresh()

             }


	 }

	}	
</script>

<style lang='less' scoped>
	.ratings{
		position: absolute;
		z-index:-10;
		top:3.48rem;
		bottom:0.92rem;
		width:100%;	
		overflow: hidden;
		font-family: "微软雅黑";
		.content1{
          top:3.6rem;
        	margin-top: 0.36rem;
        	margin-left: 0.36rem;
        	margin-right: 0.36rem;
        	

        	.left{
        		width: 2.75rem;
        		display: inline-block;
                 border-right:1px solid  rgba(7,17,27,0.1);
                 text-align: center;
                 margin-bottom: 0.36rem;
        		/* padding-top:0.36rem; */
        		 .score{
        		 	font-size: 0.48rem;
        		 	color:rgb(255,153,0);
        		 	line-height: 0.56rem;
        		 	margin-bottom: 0.12rem;

        		 }
        		 .left1{
        		 	font-size: 0.24rem;
        		 	color: rgb(7,17,27);
        		 	line-height: 0.24rem;
        		 	margin-bottom: 0.16rem;
        		 	
        		 }
        		 .left2{
        		 	font-size: 0.2rem;
        		 	color: rgb(147,153,159);
        		 	line-height: 0.2rem;
        		 	margin-bottom: 0.12rem;
        		 }

        	

        	}
        	.right{
        		width:3.9rem;
        		height:2rem;
        		
        		display: inline-block; 
        		float: right;

        		li{ 
        			        			
        			margin-left: 0.48rem;
        			line-height: 0.35rem;
        			.right1{
                      font-size: 0.24rem;
                      color: rgb(7,17,27);
                      margin-right: 0.24rem;
        			}
        			.right2{
        				font-size: 0.24rem;
        				color:rgb(147,153,159);
        				 
        			}
        			.star{
                       position: relative;
                       bottom:0.37rem;
                       left:1.2rem;
                       height:0.15rem;
        			}
        		}
        		

        	}
        	


		}

		.white1{
    width: 100%;
		background: #f3f5f7;
		position: relative;
		top:0rem;
		height:0.4rem;
		border-bottom: 2px solid rgba(7,17,27,0.1);
		border-top:2px solid  rgba(7,17,27,0.1);            
	     }
     .content2{
        margin-top: 0.36rem;
        margin-left:0.36rem;
        margin-right:0.36rem;
                  
            ul{
              list-style: none;
              display: flex;
              margin-top: 0.36rem;
              font-size: 0.24rem;

              border-bottom:1px solid  rgba(7,17,27,0.1); 
              li{
                width: 1.2rem;
                height:0.58rem;
                color:rgb(7,17,27);
                line-height: 0.58rem;
                text-align: center;
                font-size: 0.26rem;
                margin-bottom: 0.36rem;
                margin-right: 0.16rem;
                border-radius: 0.1rem;
                .number{
                 font-size: 0.2rem;
                }
              }

              .li1{
              background: skyblue;
              
              }
              .select1{
                background: rgb(0,160,220);
                color:white;
              } 
              .li2{
                background: rgba(0,160,220,0.2);
              }
               .select2{
                background: rgb(0,160,220);
                color:white;
              } 
              .li3{
                background: lightgray;
              }
              .select3{
                background: rgb(0,160,220);
                color:white;
              }
            }
         .icon2{
            margin-top:0.24rem;
            color:gray;  
            .checked{
              color:rgb(0,160,220);
           } 
         }
          

    } 

  .content3 {
     
      margin-bottom: 0.24rem;
      .rating{
     
      .menu-list {
         .rat {
          padding-top:0.36rem; 
          margin: 0.24rem  0.36rem  0  0.36rem;
          display: flex;

          border-top:1px solid rgba(7, 17, 27, 0.1);
          .pic {
            border-radius: 50%;
            width: 0.56rem;
            height: 0.56rem;
          }
          .ratR {
            margin-left: 0.24rem;
            .data{
              position: absolute;
              right: 0.36rem;
              color: #93999f;
            }
            .star {
              display: flex;
              margin: 0.08rem 0 0.12rem 0;
             
              .time {
                font-size: 0.2rem;
                font-weight: 200;
                color: rgb(147, 153, 159);
                line-height: 0.65rem;
                  height: 0.48rem;
              }
            }
            .text {
              font-size: 0.24rem;
              line-height: 0.36rem;
              color: rgb(7, 17, 27);
            }
            .satisfy {
              display: flex;
              .good {
                color: rgb(0, 160, 220);
                font-size: 0.24rem;
                line-height: 0.84rem;
              }
              .bad {
                color: rgb(147, 153, 159);
                font-size: 0.24rem;
                line-height: 0.84rem;
              }

              .foods {
                display: flex;
                span {
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                  margin: 0.16rem;
                  border: 0.02rem solid rgba(7, 17, 27, 0.1);
                  border-radius: 0.02rem;
                  background-color: white;
                  font-size: 0.18rem;
                  color: rgb(147, 153, 159);
                  padding:0.12rem ;
                  width: 1rem;
                  margin-bottom:0.36rem;

                }
              }
            }
          }
        }     
      }
    }     
  } 	     
}
</style>


